import { FC } from 'react';
import clsx from 'clsx';

import { AlignItems, JustifyContent, SpacingGaps, SpacingProps, SpacingScope } from './spacing.interface';

export const Spacing: FC<SpacingProps> = ({
  children,
  direction = 'row',
  gap = '0_25',
  justifyContent = 'start',
  alignItems = 'stretch',
}) => {
  const justifyContentClasses: { [key in JustifyContent]: string } = {
    start: 'justify-start',
    end: 'justify-end',
    center: 'justify-center',
    between: 'justify-between',
    around: 'justify-around',
    evenly: 'justify-evenly',
  };

  const alignItemsClasses: { [key in AlignItems]: string } = {
    start: 'items-start',
    end: 'items-end',
    center: 'items-center',
    baseline: 'items-baseline',
    stretch: 'items-stretch',
  };

  const gapItemsClasses: { [key in SpacingGaps]: string } = {
    '0_25': 'gap-zep-0_25',
    '0_5': 'gap-zep-0_5',
    '0_75': 'gap-zep-0_75',
    '1': 'gap-zep-1',
    '1_5': 'gap-zep-1_5',
    '2': 'gap-zep-2',
    '2_5': 'gap-zep-2_5',
    '3': 'gap-zep-3',
    '3_75': 'gap-zep-3_75',
    '4': 'gap-zep-4',
  };

  return (
    <div
      className={clsx(
        SpacingScope,
        'flex',
        {
          'flex-row': direction === 'row',
          'flex-col': direction === 'column',
        },
        justifyContentClasses[justifyContent],
        alignItemsClasses[alignItems],
        gapItemsClasses[gap],
      )}
      data-testid="zep-spacing"
    >
      {children}
    </div>
  );
};
